<template>
    <div class="newcourse">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item>{{id==-1?'新建课程设计教学':'编辑课程设计教学'}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container" v-loading="loading">
            <div class="form">
                <el-form label-width="110px">
                    <el-form-item label="教学名称：">
                        <el-input size="small" v-model="name"></el-input>
                    </el-form-item>
                    <el-form-item  size="small" label="作业提交截止：">
                        <el-col :span="11">
                            <el-date-picker
                                type="date"
                                placeholder="选择日期"
                                v-model="deadlineTime"
                                value-format="yyyy-MM-dd"
                                style="width: 100%;"
                            ></el-date-picker>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="教师提示：">
                        <el-input  size="small" type="textarea" rows="5" v-model="teacherReminder"></el-input>
                    </el-form-item>
                    <el-form-item label="学生名单：">
                        <el-table
                            class="stu-list"
                            size="mini"
                            height="330"
                            :data="workForTeacherDTOList"
                            style="width: 100%">
                            <el-table-column
                            type="index"
                              label="序号">
                            </el-table-column>
                            <el-table-column
                              label="姓名">
                              <template slot-scope="scope">
                                <span>{{ scope.row.studentName }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="入学年份">
                              <template slot-scope="scope">
                                <span>{{ scope.row.enrollmentYear }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="学号">
                              <template slot-scope="scope">
                                <span>{{ scope.row.studentNumber }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="院系">
                              <template slot-scope="scope">
                                <span>{{ scope.row.academyName }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="班级">
                              <template slot-scope="scope">
                                <span>{{ scope.row.className }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="作业项目">
                              <template slot-scope="scope">
                                <el-select size="mini" :value="scope.row.workProjectName" v-model="scope.row.workProjectId" placeholder="请选择">
                                    <el-option
                                      v-for="item in options"
                                      :key="item.id"
                                      :label="item.name"
                                      :value="item.id">
                                    </el-option>
                                  </el-select>
                              </template>
                            </el-table-column>
                            <el-table-column label="操作" width="80px">
                              <template slot-scope="scope">
                                <el-button
                                  size="mini"
                                  type="danger"
                                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                              </template>
                            </el-table-column>
                          </el-table>
                          <div class="stunumber">
                              <span>共计 {{workForTeacherDTOList.length}} 人</span>
                              <el-button type="primary" size="mini" @click="modifyStuList" plain>修改名单</el-button>
                          </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="savechange">{{id==-1?'创建教学':'确定修改'}}</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="修改学生名单" :visible.sync="editVisible" width="800px">
            <div class="select-bg">
                <el-select v-model="select1" @change="Liststudentbyparam" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options1"
                      :key="item.id"
                      :label="item.enrollmentYear"
                      :value="item.id">
                    </el-option>
                </el-select>
                <el-select v-model="select2" @change="change2" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options2"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                </el-select>
                <el-select v-model="select3" @change="Liststudentbyparam" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options3"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                </el-select>
            </div>
            <el-table
                size="mini"
                ref="multipleTable"
                class="editlist"
                :data="studentlist"
                tooltip-effect="dark"
                height="300px"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                  type="selection">
                </el-table-column>
                <el-table-column
                  type="index"
                  label="序号">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="姓名">
                </el-table-column>
                <el-table-column
                  prop="enrollmentYear"
                  label="入学年份">
                </el-table-column>
                <el-table-column
                  prop="number"
                  label="学号">
                </el-table-column>
                <el-table-column
                  prop="academyName"
                  label="院系">
                </el-table-column>
                <el-table-column
                  prop="className"
                  label="班级">
                </el-table-column>
              </el-table>
            <span slot="footer" class="dialog-footer">
                <el-checkbox class="fl" v-model="checked">随机布置作业</el-checkbox>
                <el-button size="small" @click="editVisible = false">取 消</el-button>
                <el-button size="small" type="primary" @click="substudent">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {fetchAddteaching, fetchGetteachingbyId, fetchUpdateteaching, fetchListallenrollmentyear, fetchGetallacademylist, fetchListstudentbyparam, fetchGetallworkprojectlist} from "../../axios/requests";
export default {
    name: 'course',
    data() {
        return {
          id:'',
          loading:false,

          name:'',
          deadlineTime:'',
          teacherReminder:'',
          workForTeacherDTOList:[],

          options1: [{
            id: '',
            enrollmentYear: '全部年份'
          }],
          options2: [{
            id: '',
            name: '全部院系',
            classList:[]
          }],
          options3: [{
            id: '',
            name: '全部班级'
          }],
          select1:'',
          select2:'',
          select3:'',
          studentlist:[],
          editVisible:false,
          checked:false,
          changestu:[],
          
          options:[],
        };
    },
    mounted(){
      let {pid} = this.$route.params;
      this.id = pid;
      this.Getallworkprojectlist();
      if (pid!=-1) {
        this.fetchGetteachingbyId()
      }
      this.Listallenrollmentyear()
      this.Getallacademylist()
    },
    methods: {
      
        handleEdit(index, row) {
            console.log(index, row);
        },
        handleSelectionChange(e){
          this.changestu = e;
        },
        // 删除操作
        handleDelete(index, row) {
          this.workForTeacherDTOList.splice(index, 1);
        },
        modifyStuList(){
            this.editVisible = true;
            this.Liststudentbyparam();
        },
        substudent(){
          var data = this.changestu.map(r => {
            var dat = {
              'academyName': r.academyName,
              'className': r.className,
              'enrollmentYear': r.enrollmentYear,
              'studentId': r.id,
              'studentName': r.name,
              'studentNumber': r.number,
              'workProjectId': '',
              'workProjectName': '',
            }
            return dat;
          })
          this.workForTeacherDTOList = this.workForTeacherDTOList.concat(data);
          this.editVisible =false;
        },
        savechange(){
          if (this.id==-1) {
            this.Addteaching()
          }else{
            this.Updateteaching()
          }
        },
        async Updateteaching() {//修改作业
          let {id, name, teacherReminder, deadlineTime, workForTeacherDTOList} = this;
          var ret = false;
          workForTeacherDTOList.forEach(item =>{
            if (item.workProjectId=="") {
              this.$message({
                message: '请为'+item.studentName+'选择作业项目',
                type: 'warning'
              });
              ret = true;
              return;
            };
          })
          if (ret) {
            return;
          };
          this.loading = true;
          var works = workForTeacherDTOList.map(r => {
            let item =  {'workStudentId':r.studentId,'workProjectId':r.workProjectId};
            return item;
          })
          try {
            let response = await fetchUpdateteaching(id, name, teacherReminder, this.$timeChange(deadlineTime), works);
            if (response.status.code ==0) {
              this.$message({
                message: '课程设计教学修改成功',
                type: 'success'
              });
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
        async Addteaching() {//新建作业
            let {name, teacherReminder, deadlineTime, workForTeacherDTOList} = this;
          var ret = false;
          workForTeacherDTOList.forEach(item =>{
            if (item.workProjectId=="") {
              this.$message({
                message: '请为'+item.studentName+'选择作业项目',
                type: 'warning'
              });
              ret = true;
              return;
            };
          })
          if (ret) {
            return;
          };
          this.loading = true;
          var works = workForTeacherDTOList.map(r => {
            let item =  {'workStudentId':r.studentId,'workProjectId':r.workProjectId};
            return item;
          })
          try {
            let response = await fetchAddteaching(name, teacherReminder, this.$timeChange(deadlineTime), works);
            if (response.status.code ==0) {
              this.$message({
                message: '课程设计教学创建成功',
                type: 'success'
              });
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
        async fetchGetteachingbyId() {//得到教学详情
          this.loading = true;
          let {id} = this;
          try {
            let response = await fetchGetteachingbyId(id);
            if (response.status.code ==0) {
              this.name=response.result.name;
              this.deadlineTime=response.result.deadlineTime;
              this.teacherReminder=response.result.teacherReminder;
              this.workForTeacherDTOList=response.result.workForTeacherDTOList;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
        change2(e){
          var dat = [{
            id: '',
            name: '全部班级'
          }];
          this.select3 = ''
          var data = this.options2.filter(r => r.id==e);
          this.options3 = dat.concat(data[0].classList);
          this.Liststudentbyparam();
        },
        async Listallenrollmentyear() {//得到入学年份列表
          let {id} = this;
          try {
            let response = await fetchListallenrollmentyear();
            if (response.status.code ==0) {
              this.options1 = this.options1.concat(response.result.list);
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            
          }
        },
        async Getallacademylist() {//得到院系
          let {id} = this;
          try {
            let response = await fetchGetallacademylist();
            if (response.status.code ==0) {
              this.options2 = this.options2.concat(response.result.list);
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            
          }
        },
        async Liststudentbyparam() {//弹框得到学生
          let {select1, select2, select3} = this;
          try {
            let response = await fetchListstudentbyparam(select1, select2, select3);
            if (response.status.code ==0) {
              this.studentlist = response.result.list;
              this.changestu = [];
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            
          }
        },
         async Getallworkprojectlist() {//所有作业项目
          try {
            let response = await fetchGetallworkprojectlist();
            if (response.status.code ==0) {
              this.options = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            
          }
        },
    }
};
</script>

<style lang='scss'>

</style>

<style lang='scss' scoped>
    .newcourse{
        .crumbs{
          padding-left:20px;
        }
        .stu-list{
            border:1px solid #ccc;
        }
        .stunumber{
            padding:10px 0;
            color:#666;
            span{
                margin-right:40px;
            }
        }
        .editlist{
            margin-top:20px;
            border:1px solid #eee;
        }
    }
</style>


